import { Component, Host, h, Element } from '@stencil/core';

@Component({
  shadow: true,
  tag: 'dc-ui-family-result-item',
  styleUrl: 'dc-ui-family-result-item.scss',
})
export class DcUiFamilyResultItem {
  @Element() el: HTMLElement;
  hasTopBorder: boolean = false;

  componentWillLoad() {
    const prevEl = this.el.previousElementSibling;
    if (prevEl?.tagName.toLowerCase() !== 'dc-ui-family-result-item') {
      this.hasTopBorder = true;
    }
  }

  render() {
    return (
      <Host>
        <div
          class={{
            'dc-ui-family-result-item': true,
            'dc-ui-family-result-item__first': this.hasTopBorder,
          }}>
          <div class="dc-ui-family-result-item__grid">
            <slot name="description-area"></slot>
            <slot name="image-area"></slot>
            <slot name="additional-content-area"></slot>
            <div class="dc-ui-family-result-item__sidebar">
              <slot name="secondary-conversion-area"></slot>
              <slot name="primary-conversion-area"></slot>
            </div>
          </div>
        </div>
      </Host>
    );
  }
}
