.dc-ui-family-result-item {
  border-bottom: 1px solid $color-grey-03;
  padding: 24px 16px;

  @media (max-width: 767px) {
    padding: 24px 0;
  }

  &__first {
    border-top: 3px solid $color-grey-02;
  }

  &__grid {
    display: grid;
    grid-template-columns: 120px 1fr calc(264px + 32px);
    row-gap: 16px;
    grid-template-areas:
      'description-area description-area sidebar-area'
      'image-area additional-content-area sidebar-area';

    @media (max-width: 767px) {
      grid-template-columns: 1fr;
      row-gap: unset;
      grid-template-areas:
        'image-area'
        'description-area'
        'additional-content-area'
        'sidebar-area';
    }
  }

  &__sidebar {
    grid-area: sidebar-area;
    margin-left: 32px;
    display: grid;
    grid-template-columns: 1fr;
    gap: 24px;
    grid-template-areas:
      'secondary-conversion-area'
      'primary-conversion-area';

    @media (max-width: 767px) {
      margin-left: 0;
    }
  }
}

::slotted([slot='image-area']) {
  grid-area: image-area;
  border: 1px solid $color-grey-03;
  height: fit-content;

  @media (max-width: 767px) {
    margin-bottom: 16px;
  }
}

::slotted([slot='description-area']) {
  grid-area: description-area;

  @media (max-width: 767px) {
    margin-bottom: 16px;
  }
}

::slotted([slot='additional-content-area']) {
  grid-area: additional-content-area;
  margin-left: 16px;

  @media (max-width: 767px) {
    margin-left: 0;
    margin-bottom: 24px;
  }
}

::slotted([slot='secondary-conversion-area']) {
  grid-area: secondary-conversion-area;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

::slotted([slot='primary-conversion-area']) {
  grid-area: primary-conversion-area;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}
